function isWeekEnd(day) {
  return day === 0 || day === 6;
}

function isToday(compareDate) {
  var today = new Date();
  var todayStr = today.getFullYear() + '-' + today.getMonth() + '-' + today.getDate();
  var compareDateStr = compareDate.getFullYear() + '-' + compareDate.getMonth() + '-' + compareDate.getDate();
  return todayStr === compareDateStr;
}

function dayToMilliseconds(day) {
  return day * 24 * 60 * 60 * 1000;
}

function isSelectedDate(calendarDate, selectedDate) {
  var calendarDateStr = calendarDate.getFullYear() + '-' + calendarDate.getMonth() + '-' + calendarDate.getDate();
  var selectedDateStr = selectedDate.getFullYear() + '-' + selectedDate.getMonth() + '-' + selectedDate.getDate();
  return calendarDateStr === selectedDateStr;
}

function getTwoDigits(number) {
  if (number >= 10) {
    return number;
  } else {
    return '0' + number;
  }
}

function dateYYYYMMDDFormat(date) {
  return date.getFullYear() + '-' + getTwoDigits(date.getMonth() + 1) + '-' + getTwoDigits(date.getDate());
}

function dateYYYYMMDDHHMMFormat(date) {
  return date.getFullYear() + '-' + getTwoDigits(date.getMonth() + 1) + '-' + getTwoDigits(date.getDate()) + ' ' + getTwoDigits(date.getHours()) + ':' + getTwoDigits(date.getMinutes());
}

module.exports = {
  isWeekEnd: isWeekEnd,
  isToday: isToday,
  dayToMilliseconds: dayToMilliseconds,
  isSelectedDate: isSelectedDate,
  dateYYYYMMDDFormat: dateYYYYMMDDFormat,
  dateYYYYMMDDHHMMFormat: dateYYYYMMDDHHMMFormat
};

